<script>
    import axios from 'axios';
    import * as d3 from 'd3';
    import { interpolateViridis } from 'd3-scale-chromatic';

    let query = '';
    let umapData = [];
    let text = '';

    let userId = 6043;
    let userPhenotype = `
    Ability to Tan	No
    number of biological children	0
    Fish Preference	Like
    Taste of broccoli	Ok - better cooked in stir fry
    hair colour	Red
    Do you grind your teeth	Yes
    Eyebrow Mover	Left eyebrow can be raised only
    Lactose intolerance	Lactose-tolerant
    Morton's Toe	No
    Cystic Fibrosis Like Disease	No
    Hair Color	red
    Wake up preference	Early riser
    Political Compass	Economic Left/Right: -4.75 Social Libertarian/Authoritarian: -4.15
    The Dress: Perception of colour	White and gold
    Pale	I have very light skin
    Academic degree	Bachelors
    mouth size	Medium
    Acrophobia	Yes
    Mental Disease	Anxiety and depression
    Strabismus	False
    Energy Level	Low
    how do you like your steak?	Medium rare
    Interested in news from real newspaper / news from the Internet	sometimes
    Foot length	10"
    Can You Spread Your Toes?	No
    Sports interest	No interest in sports
    Tea consumption	Rarely
    Beard Color	Red, moustache still is, beard mostly white
    Widow's Peak	No
    Dyslexia	No
    Income Range?	$140k
    Sexual Preferences	Same sex attraction
    Allergy to artificial grape flavoring	No
    Webbed toes	None
    Good / poor eater as child	Eat a lot of different things
    Heroin addiction	No
    Motion sickness	When i was younger
    Birth year	1969
    Third Nipple	None
    Hypermobility	None
    Hair colour	red
    Can you smell cut-grass?	Yes
    Irritable Bowel Syndrome	No
    Sexuality	Exclusively homosexual
    `;

    async function fetchUmap(event) {
        if (event && event.key !== 'Enter') {
            return;
        }

        try {
            const response = await axios.get(`https://ec2-18-223-122-200.us-east-2.compute.amazonaws.com:5000/api?user_id=${userId}&query=${query}&user_phenotype=${userPhenotype}`);
            umapData = response.data.umapData;
            text = response.data.text;
            query = '';
            drawUmap();
        } catch (error) {
            console.error(error);
        }
    }


    function drawUmap() {
        if (!umapData || !text) {
            return;
        }

        const margin = {top: window.innerHeight*0.05, right: window.innerWidth*0.05, bottom: window.innerHeight*0.05, left: window.innerWidth*0.05},
        width = window.innerWidth*0.9 - margin.left - margin.right,
        height = window.innerHeight*0.9 - margin.top - margin.bottom;

        let svg = d3.select('#umap').select('svg');
        if (svg.empty()) {
            svg = d3.select('#umap').append('svg')
                .attr('width', width + margin.left + margin.right)
                .attr('height', height + margin.top + margin.bottom)
                .append('g')
                .attr('transform', `translate(${margin.left},${margin.top})`);
        }

        const xScale = d3.scaleLinear()
            .domain(d3.extent(umapData, d => d.x))
            .range([0, width]);

        const yScale = d3.scaleLinear()
            .domain(d3.extent(umapData, d => d.y))
            .range([height, 0]);

        const labels = [...new Set(umapData.map(d => d.label))];

        const labelIndex = d3.scaleOrdinal()
            .domain(labels)
            .range(d3.range(labels.length));

        const labelScale = d3.scaleLinear()
            .domain(d3.extent(umapData, d => labelIndex(d.label)))
            .range([0, 1]);

        const colorScale = d3.scaleSequential()
            .domain([0.5, 1])
            .interpolator(t => interpolateViridis(labelScale(t)));

        const data = umapData.map((d, i) => ({ x: xScale(d.x), y: yScale(d.y), label: d.label, user: d.user }));

        const circle = svg.selectAll('circle').data(data);

        circle.join(
            enter => enter.append('circle')
                .attr('r', d => d.user ? 10 : 3)
                .attr('cx', d => d.x)
                .attr('cy', d => d.y)
                .attr('fill', d => colorScale(labelIndex(d.label))) // updated fill attribute
                .attr('class', d => d.user ? 'user' : 'non-user')
                .style('opacity', 0)
                .transition().duration(1000).style('opacity', 1),
            update => update
                .call(update => update.transition()
                    .duration(1000)
                    .attr('cx', d => d.x)
                    .attr('cy', d => d.y)
                    .attr('fill', d => colorScale(labelIndex(d.label))) // updated fill attribute
                    .attr('r', d => d.user ? 10 : 3)
                    .style('opacity', 1)),
            exit => exit
                .call(exit => exit.transition()
                    .duration(1000)
                    .style('opacity', 0).remove())
        );

        // const labels = [...new Set(data.map(d => d.label))];
        const labelData = labels.map(label => {
            const clusterPoints = data.filter(d => d.label === label);
            return {
                label,
                x: d3.mean(clusterPoints, d => d.x),
                y: d3.mean(clusterPoints, d => d.y)
            };
        });

        const labelText = svg.selectAll('text').data(labelData);
        labelText.join('text')
            .attr('x', d => d.x)
            .attr('y', d => d.y)
            .attr('text-anchor', 'middle')
            .attr('dominant-baseline', 'middle')
            .attr('fill', 'black')
            .style('font-size', '14px')
            .style('background-color', 'white')
            .text(d => d.label);

    }
</script>


<style>
    @import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap');
    @import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap');

    #umap {
        position: fixed;
        top: 5%;
        left: 8%;
        width: 90%;
        height: 90%;
    }

    #title {
        padding-left:30px;
        padding-top:20px;
    }

    input {
        width: 30%;
        height: 35px;
        border-radius: 15px;
        font-size: 16px;
        padding: 0 10px;
        background-color: white;
        font-family: 'Rubik', serif;
        z-index: 9999;
        
    }

    h1, p {
        font-family: 'Libre Baskerville', serif;
    }

    p {
        background-color: white;
        font-family: 'Rubik', serif;
        z-index: 9999;
    }

    #ai {
        max-width: 450px;
    }
</style>

<div id ='title'>
<h1>🧬 My DNA story</h1>
<div id='umap'></div>
<input bind:value={query} on:keyup={fetchUmap} placeholder='Ask away!' />
<p id='ai'>{text}</p>
</div>